/* Preloader */
#preloader, .div-preloader {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
  background-image: url(/images/admin-panel/bg.jpg);
  z-index: 99999;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.div-preloader {
  position: absolute;
  background-image: none;
  background-color: rgba(255, 255, 255, 0.5);
  display: none;
  z-index: 1000;
}

#status, .div-status {
  position: absolute;
  left: 50%;
  top: 50%;
  margin: auto;
  width: 50px;
  height: 50px;
  margin-top: -25px;
  margin-left: -25px;
  display: inline-block;
  vertical-align: middle;
  background: #333;
  background-repeat: no-repeat;
  background-size: 100%;
  border-radius: 50%;
  animation: 1.15s infinite ease-in-out;
  -webkit-animation: 1.15s infinite ease-in-out;
  -moz-animation: 1.15s infinite ease-in-out;
  animation-name: loader;
  -webkit-animation-name: loader;
  -moz-animation-name: loader;
}

#status:before, #status:after {
  animation: 1.15s infinite ease-in-out;
  -webkit-animation: 1.15s infinite ease-in-out;
  -moz-animation: 1.15s infinite ease-in-out;
}

#status:before, #status:after {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes loader {
  from {
    transform: scale(0);
    opacity: 1;
  }
  to {
    transform: scale(1);
    opacity: 0.5;
  }
}

@-ms-keyframes loader {
  from {
    -ms-transform: scale(0);
    opacity: 1;
  }
  to {
    -ms-transform: scale(1);
    opacity: 0.5;
  }
}

@-webkit-keyframes loader {
  from {
    -webkit-transform: scale(0);
    opacity: 1;
  }
  to {
    -webkit-transform: scale(1);
    opacity: 0.5;
  }
}

@-moz-keyframes loader {
  from {
    -moz-transform: scale(0);
    opacity: 1;
  }
  to {
    -moz-transform: scale(1);
    opacity: 0.5;
  }
}

